Drupal.behaviors.formsCustom = {
  attach(context, settings) {
    const passwordContainers = context.querySelectorAll(
      '.input-password-custom',
    );

    passwordContainers.forEach((container) => {
      const input = container.querySelector('input');
      const toggleButton = container.querySelector('.button-eye-icon');
      if (input && toggleButton) {
        if (toggleButton.classList.contains('show')) {
          input.setAttribute('type', 'text');
        } else {
          input.setAttribute('type', 'password');
        }

        toggleButton.addEventListener('click', () => {
          toggleButton.classList.toggle('show');

          const isPasswordVisible = toggleButton.classList.contains('show');
          input.setAttribute('type', isPasswordVisible ? 'text' : 'password');
        });
      }
    });
  },
};
